<template>
  <div class="home">
      <img src="../assets/logo.png">
    <h1>{{ msg }}</h1>
    <mu-text-field v-model="address" label="请输入正确的TTT钱包地址" label-float :error-text="error_message" icon="account_balance_wallet"></mu-text-field>
    <mu-button @click="airdrop" :disabled="disabled" :type="type" :color="type">{{btn_title}}</mu-button>
    <p @click="openSimpleDialog">没有TTT钱包？立刻下载</p>
    <mu-dialog title="下载TTT钱包" width="360" :open.sync="openSimple">
        
        <a href="http://ttt-xg-1253605028.coshk.myqcloud.com/trustnote.apk" target="_blank">
            <img src="../assets/android.png">
        </a>
        <a href="https://www.pgyer.com/TrustNote" target="_blank">
            <img src="../assets/ios.png">
        </a>
        <a href="http://ttt-xg-1253605028.coshk.myqcloud.com/trustnote-win64.exe" target="_blank">
            <img src="../assets/windows.png">
        </a>
        <a href="http://ttt-xg-1253605028.coshk.myqcloud.com/trustnote-osx64.dmg" target="_blank">
            <img src="../assets/mac.png">
        </a>
        <a href="http://ttt-xg-1253605028.coshk.myqcloud.com/trustnote-linux64.zip" target="_blank">
            <img src="../assets/linux.png">
        </a>
        <mu-button slot="actions" flat color="primary" @click="closeSimpleDialog">Close</mu-button>
    </mu-dialog>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
import { Indicator } from 'mint-ui';
export default {
  name: 'Home',
  data () {
    return {
      openSimple:false,
      address:"",
      error_message:"",
      state:"",
      btn_title:"输入地址后立刻领取",
      msg: 'LOGO 空投进行中',
      style:"ok",
      disabled:true,
      type:"default"
    }
  },methods:{
      openSimpleDialog(){
          this.openSimple=true
      },
      closeSimpleDialog(){
          this.openSimple=false
      },
      airdrop(){
          //
          if(localStorage.getItem("address")===this.address){
              MessageBox('抱歉',"您已经领取过啦，每个人只能领取一次 ~ ");
          }else{
              Indicator.open({
                text: '正在领取中...',
                spinnerType: 'fading-circle'
                });
              this.$http.post('/api/pay',{to:this.address,amount:10},{emulateJSON: true}).then(response => {
                // get body data
                //this.msg = response.body;
                console.log(response.body)
                Indicator.close();
                //localStorage.setItem("address", this.address);
                MessageBox('恭喜', "领取成功啦 ~");

            }, response => {
                // error callback
                Indicator.close();
                MessageBox('错误',"抢LOGO的人太多，服务器不堪重负，离家出走了~ ");
            });
            console.log(this.address)
          }
          
      }
  },
  watch: {
    address: function (val, oldVal) {
        console.log(val)
        var regEth = /^[0-9A-Z]{32}$/;
        console.log(regEth.test(val))
        if(regEth.test(val)){
            
            this.disabled=false
            this.type="primary"
            this.error_message=""
            this.btn_title="立刻领取"
        }else{
            this.disabled=true
            this.type="default"
            this.error_message="输入正确地址，才能领取"
            this.btn_title="输入正确地址，才能领取"
        }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input.ok{
    border: none;
}
input.err{
    border: solid 1px #f00;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



